import * as React from "react"
import { Link } from "gatsby"
import "./postcard.css"
import { GatsbyImage } from "gatsby-plugin-image"


const postcard = (args) => {
    const post = args.post
    const title = post.title || "no title"
    let thumb = <></>
    // if (post.frontmatter.thumbnail != null) {
    //     thumb = <GatsbyImage image={post.frontmatter.thumbnail.childImageSharp.gatsbyImageData} alt="Thumbnail"/>
    // }
    // else {
    //     thumb = <></>
    // }
    return (
      <li>
        <Link to={post.id} itemProp="url">
          <article
            className="post-list-item"
            itemScope
            itemType="http://schema.org/Article"
          >
            <div className="post-list-leftside">
              <header>
                <h2>
                  <span itemProp="headline">{title}</span>
                </h2>
                <small>{post.created_time}</small>
              </header>
              <section>
                {/* <p
                  dangerouslySetInnerHTML={{
                    __html: post.description
                  }}
                  itemProp="description"
                /> */}
              </section>
            </div>
            {/* <div className="post-list-rightside">{thumb}</div> */}
          </article>
        </Link>
      </li>
    )
}

export default postcard